
import moment from 'moment'
import util from '@/libs/util'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default{
mixins: [ common,globals ],
components: {
Tab,
Popup,
TabItem
},
data () {
return {
tab: 0,
time: moment(new Date()).format('YYYY-MM-DD'),
data: {},
ready: false,
barAction: false,
popup: {
show: false,
title: '',
amount: 0,
unit: '',
filed: '',
list: []
}
}
},
watch: {
tab: function (val) {
this.init_data()
}
},
mounted () {
this.init_data()
},
methods: {
getPercent (row) {
let percent = 0,
field = this.popup.field,
total = this.popup.amount
percent = parseFloat((row[field]/total)*100).toFixed(2)

return percent
},
back () {
this.popup.show = false
this.barAction = false
},
detail (title,type,field,unit) {
if(this.getNums(type,field)>0){
this.popup.title = title
this.popup.list = this.data[type][field]
this.popup.field = field
this.popup.unit = unit||'人'
this.popup.amount = this.getNums(type,field)
this.popup.show = true
setTimeout(()=>{
this.barAction = true
},100)
}
},
init_data () {
this.ready = false
this.dr().getData(this.data_uri)
.then(data=>{
this.data = data
this.ready = true
})
},
getNums (type,field) {
let list = this.data[type][field],
result = 0
if(list&&list.length>0){
list.forEach(l=>{
result += Number(l[field])
})
if(!_.isNumber(list[list.length-1][field])){
result = parseFloat(result).toFixed(2)
}
}
return result
}
},
computed: {
title () {
let time = moment(new Date()).format('YYYY年MM月DD')
let weeks = moment(new Date()).isoWeekday()
let mapWeek = {1:'一',2:'二',3:'三',4:'四',5:'五',6:'六',7:'日'}
return time + ' 星期' + mapWeek[weeks]
},
data_uri () {
let uri = '',
start_date = '',
end_date = '',
weeks = moment(this.time).weekday() - 1

if(this.tab==0){
start_date = moment(this.time).subtract(weeks,'days').format('YYYYMMDD')
end_date = moment(this.time).format('YYYYMMDD')
}else{
start_date = moment(this.time).format('YYYYMM')+'01'
end_date = moment(this.time).format('YYYYMM')+moment(moment(this.time).format('YYYY-MM'),'YYYY-MM').daysInMonth()
}

return util.sprintf('report_summaries/overview?start_day=%s&end_day=%s',start_date,end_date)
}

}
}



